import {
	mapState
} from 'vuex'
import {
	getHomeList,
	addHit
} from '../../api/mainApi.js'
import {
	appUpdate
} from '../../api/userApi.js'
import T from '@/utils/tips.js'
import TabBar from '@/components/common/TabBar.vue'
export default {
	data() {
		return {
			checkIndex: 0,
			homeList: {},
			banner: [], // 轮播图
			navs: [], // 导航nav
			advs: [], // 广告
			seles: [], // 精选
			indicatorDots: true,
			autoplay: true,
			interval: 3000,
			duration: 500,
			platform: 0
		}
	},
	components: {
		TabBar
	},
	onTabItemTap(e){
		uni.setStorageSync('pagePath','main')
	},
	onLoad() {
		uni.setStorageSync('pagePath','main')
		// 版本更新 （APP）





		// 设备样式兼容
		this.platform = uni.getStorageSync('platform');
	},
	onShow() {
		uni.hideLoading();
		// 获取首页banner
		this.getHomeList()
	},
	onPullDownRefresh() {
		//监听下拉刷新动作的执行方法，每次手动下拉刷新都会执行一次
		console.log('refresh');
		// 获取首页banner
		this.getHomeList()
		setTimeout(function() {
			uni.stopPullDownRefresh(); //停止下拉刷新动画
		}, 1000);
	},
	methods: {

		// 更新版本
		updataApp() {

































































































































		},
		// 去广告页面
		goadSet(item) {
			addHit({
				id: item.id
			})
			if (item.type == 1) {
				uni.navigateTo({
					url: '/pages/common/webview/webview?url=' + item.url
				})
			} else if (item.type == 5) {
				uni.navigateTo({
					url: '/pages/order/goodsDetail/goodsDetail?shopId=1&goodsId=' + item.url
				})
			}
		},
		// nav 去搜索页面
		goSearchPage(name) {
			uni.navigateTo({
				url: '/pages/order/goodsList/goodsList?search=' + name
			})
		},
		// 去搜索页面
		goSearch() {
			uni.navigateTo({
				url: '/pages/main/search/search'
			})
		},
		// 获取首页banner
		getHomeList() {
			getHomeList({
				parentId: 1
			}).then((res) => {
				if (res.code == '1000') {
					this.homeList = res.data
					this.homeList.list[3].list[1].goodsDetailRespList.forEach((item, index) => {
						item.valueAddr = item.valueAddr.substring(0, 5)
					})
				}
			})
		},
		goNextPage(item) {

			addHit({
				id: item.id
			})

			if (item.type == 5) {
				uni.navigateTo({
					url: '/pages/order/goodsDetail/goodsDetail?shopId=' + item.shopId + '&goodsId=' + item.id
				});
			} else if (item.type == 1) {
				uni.navigateTo({
					url: '/pages/common/webview/webview?url=' + item.url
				})
			}
		},
		goGoodsDetail(shopId, goodsId) {
			uni.navigateTo({
				url: '/pages/order/goodsDetail/goodsDetail?shopId=' + shopId + '&goodsId=' + goodsId
			})
		}
	}
}
